html {
  font-size: 16px; }

body {
  background-color: #03183A; }

.App {
  overflow: hidden;
  position: relative;
  color: #fff; }

.navbar-fixed {
  position: fixed !important;
  top: -60px !important;
  animation-name: showNav;
  animation-duration: 400ms;
  animation-fill-mode: forwards; }

.canvas-fixed {
  position: fixed !important;
  top: -100vh !important;
  margin-top: -60px;
  animation-name: showCanvas;
  animation-duration: 400ms;
  animation-fill-mode: forwards; }

.li-active {
  color: #ffcf4b;
  text-shadow: 0 0 6px white !important; }

h1 {
  margin: 0;
  padding: 80px 0;
  text-align: center;
  color: whitesmoke;
  text-transform: uppercase;
  font-weight: 400;
  transform: translateX(-30px);
  opacity: 0; }
  h1[revealed] {
    transform: translate(0);
    opacity: 1;
    transition: opacity 800ms, transform 800ms; }

@keyframes showNav {
  to {
    transform: translateY(60px); } }

@keyframes showCanvas {
  to {
    margin-top: 0; } }
