.Contact {
  position: relative;
  background: linear-gradient(to bottom, #3e84f4 10%, #a8c7fa 100%);
  height: fit-content; }
  .Contact h1 {
    padding-bottom: 0; }
  .Contact h2 {
    color: #00296c;
    text-align: center;
    font-weight: normal;
    margin: 20px auto;
    transform: translateX(-30px);
    opacity: 0; }
    .Contact h2[revealed] {
      transform: translate(0);
      opacity: 1;
      transition: opacity 800ms, transform 800ms; }
  .Contact .Compose {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap-reverse;
    width: 100%;
    height: 400px; }
    .Contact .Compose > * {
      transform: scale(0);
      opacity: 0; }
      .Contact .Compose > *[revealed] {
        transform: scale(1);
        opacity: 1;
        transition: transform 0.5s cubic-bezier(0.17, 0.67, 0.41, 1.22), opacity 450ms 50ms; }
        .Contact .Compose > *[revealed][type=submit] {
          transition: background-color 400ms ease, transform 0.5s cubic-bezier(0.17, 0.67, 0.41, 1.22), opacity 450ms 50ms, color 400ms; }
    .Contact .Compose > section {
      position: relative; }
      .Contact .Compose > section [name] {
        width: 100%;
        padding: 12px;
        margin: 5px 0;
        border-radius: 4px;
        border: none;
        outline: none;
        background-color: #062860; }
      .Contact .Compose > section [valid=false] {
        box-shadow: 0 0 5px red; }
      .Contact .Compose > section span {
        color: red;
        position: absolute;
        top: 3px;
        right: 3px; }
    .Contact .Compose button {
      display: flex;
      justify-content: center;
      text-decoration: none;
      outline: none;
      width: 25%;
      background-color: #062860;
      border: 0;
      padding: 8px;
      border-radius: 4px;
      color: #e0e0e0;
      transition: background-color 400ms ease, color 400ms; }
      .Contact .Compose button:hover {
        background-color: #88fb8e;
        color: #000; }
        .Contact .Compose button:hover svg {
          transition: transform 300ms ease-out;
          transform: rotate(360deg); }
  .Contact .col {
    position: relative;
    margin: 0 auto;
    float: unset !important;
    transition: opacity 400ms, transform 400ms cubic-bezier(0.9, -0.32, 0.83, 0.67); }
  .Contact .success {
    margin: 0 auto;
    width: 100%;
    height: 200px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: transform 700ms cubic-bezier(0.17, 0.67, 0.14, 1);
    transform: scale(0);
    display: none;
    position: relative; }
    .Contact .success i {
      position: relative;
      height: 50%;
      width: auto; }
      .Contact .success i svg {
        width: auto;
        height: 100%; }
      .Contact .success i > div {
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 100%;
        margin: auto;
        transform: scale(0.97);
        overflow: hidden;
        display: flex;
        flex-direction: row-reverse; }
        .Contact .success i > div > div {
          width: 100%;
          height: 100%;
          background-color: #00e500; }
    .Contact .success p {
      color: white;
      text-align: center;
      opacity: 1;
      width: 100%;
      font-size: 1.2rem; }

.scaleToZero {
  transform: scale(0) !important; }

.scaleToOne {
  transform: scale(1) !important; }

.displayToFlex {
  display: flex !important; }

.displayToNone {
  display: none !important; }

@media only screen and (min-width: 400px) {
  .Contact .success p {
    max-width: 392px; } }
