.Footer {
  height: 119px;
  width: 100%;
  background-color: #03183A;
  display: flex;
  align-items: center; }
  .Footer ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: fit-content;
    margin: auto;
    list-style: none;
    padding: 0; }
    .Footer ul li {
      display: block;
      margin: 0 15px; }
      .Footer ul li a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 55px;
        height: 55px;
        border-radius: 10px;
        background-color: rgba(168, 200, 250, 0.564);
        transition: background-color 400ms, transform 400ms; }
        .Footer ul li a svg {
          color: #fff; }
        .Footer ul li a:hover {
          transform: scale(0.95);
          transition: background-color 400ms, transform 400ms cubic-bezier(0.05, 0.76, 0.35, 2.41); }
  .Footer li:nth-of-type(1) a:hover {
    background-color: #4267B2; }
  .Footer li:nth-of-type(2) a:hover {
    background-color: #24292E; }
  .Footer li:nth-of-type(3) a:hover {
    background-color: #0077B5; }
  .Footer li:nth-of-type(4) a:hover {
    background-color: #FF4500; }

@media only screen and (max-width: 575px) {
  .Footer {
    height: 90px; }
    .Footer ul li {
      margin: 0 10px; }
      .Footer ul li a {
        width: 45px;
        height: 45px; }
        .Footer ul li a svg {
          height: 70%;
          width: 70%; } }
